body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {

  background: #8EC0E8;
;
  display: flex;
  flex-direction: column;
  align-items: center;
}

table {
  border-spacing: 0px;
  width: 100%;
}

th {
  text-align: start;
  padding: 5px;
  color: #44363;
}

td {

  padding: 5px;
  max-width: 700px;
  vertical-align: top;
  color: #443635;
}

.number {
  text-align: center;
}
tbody > tr:nth-child(odd) {
  background: pink;
}

tbody > tr:nth-child(even) {
  background: #8EC0E8;
}

h1 {
  color: #443635;
}

.filter {
  font-size: 16px;
  width: 100%;
}

